import React, { Component } from 'react';
import Menu from './Menu';
import bread from './bread.json';
import './App.scss';

class App extends Component {
  render() {
    const breadList = bread;

    return (
      <div className="MenuList">
        <h1>이디야제과</h1>
        <ul className="cf">
          {
            // 배열.map(매개변수1,매개변수2)
            // 매개변수1 : 배열요소 값 , 매개변수2 : 배열요소 인덱스값
            // bread.map( (data, idx) => <Menu key={idx} name={data.name} picture={data.image} /> )
            // 인덱스 idx를 key값으로 사용하면, id 값이 필요없다.
            breadList.map( data => <Menu key={data.id} name={data.name} picture={data.image} /> )
          }
        </ul>
      </div>
    );
  }
}

export default App;